import React from 'react';
import Button from '@mui/material/Button';



interface ButtonProps {
  onClick: () => void;
  disabled?: boolean;
  className?: string;
  fgColor?:[primary|secondary];
  children: React.ReactNode;
}

const MiniteButton: React.FC<ButtonProps> = ({ onClick, disabled = false, className = '',fgColor='primary', children }) => {
  return (
   
    <Button variant="contained" color={fgColor}> {children} </Button>
   
  );
};

export default MiniteButton;
